import React from "react";
import { Link } from "react-router-dom";
import startworkout from "./exercise.svg";
import Homeicon from "./icon.svg";
import connect from "./watch.svg";
import firebase from "./firebase";

function Workoutpage() {
  // royden java code
  const serviceUUID = "e267751a-ae76-11eb-8529-0242ac130003";
  const startCharacteristicUUID = "19b10012-e8f2-537e-4f6c-d104768a1214";
  const pauseCharacteristicUUID = "6995b940-b6f4-11eb-8529-0242ac130003";
  const activityUUID = "00002a19-0000-1000-8000-00805f9b34fb";

  let device;
  let server;
  let service;
  let startCharacteristic;
  let pauseCharacteristic;
  let activityCharacteristic;
  let value;

  let lungesCounter = 0;
  let crunchesCounter = 0;
  let pushupsCounter = 0;

  async function requestDevice() {
    try {
      console.log("Searching For Fitness Band...");

      device = await navigator.bluetooth.requestDevice({
        filters: [{ name: "Fitness-Band" }],
        optionalServices: [serviceUUID],
      });

      console.log("Connected To Fitness Band Successfully!");

      const abortController = new AbortController();

      server = await device.gatt.connect();

      service = await server.getPrimaryService(serviceUUID);

      startCharacteristic = await service.getCharacteristic(
        startCharacteristicUUID
      );
      pauseCharacteristic = await service.getCharacteristic(
        pauseCharacteristicUUID
      );
      activityCharacteristic = await service.getCharacteristic(activityUUID);

      console.log("All Characteristics added successfully!");

      console.log("Watching advertisements from " + device.name + "...");

      device
        .watchAdvertisements({ signal: abortController.signal })
        .catch((error) => {
          console.log("Error: " + error);
        });
    } catch (error) {
      console.log("Failed: " + error);
    }
  }

  function startActivity() {
    var arr = new Int8Array([21, 31]);
    return startCharacteristic.writeValueWithResponse(arr).then((response) => {
      return activityCharacteristic.startNotifications().then((_) => {
        console.log("Started Notifications");
        activityCharacteristic.addEventListener(
          "characteristicvaluechanged",
          function (event) {
            // Call activityDataTransfer function after a delay to ensure that the data is loaded
            setTimeout(function () {
              activityDataTransfer(event);
            }, 500);
          }
        );
      });
    });
  }

  //Function to Stop activity
  function stopActivity() {
    var arr = new Int8Array([21, 31]);
    return pauseCharacteristic.writeValueWithResponse(arr).then((response) => {
      return activityCharacteristic
        .stopNotifications()
        .then((_) => {
          console.log("Stopped Notifications");
        })
        .catch((error) => {
          console.log("Argh! " + error);
        });
    });
  }

  const db = firebase.database();
  function activityDataTransfer(event) {
    value = event.target.value.getInt8();
    console.log(value);
    if (value == 0) {
      lungesCounter = lungesCounter + 1;
      var elem = document.querySelector("#lungesCount");
      if (elem) {
        elem.innerHTML = lungesCounter;
      }
    } else if (value == 1) {
      crunchesCounter = crunchesCounter + 1;
      var elem = document.querySelector("#crunchesCount");
      if (elem) {
        elem.innerHTML = crunchesCounter;
      }
    } else if (value == 2) {
      pushupsCounter = pushupsCounter + 1;
      var elem = document.querySelector("#pushupsCount");
      if (elem) {
        elem.innerHTML = pushupsCounter;
      }
    }

    // send data to firebase
    db.ref("activity").set({
      lungesCounter: lungesCounter,
      crunchesCounter: crunchesCounter,
      pushupsCounter: pushupsCounter,
    });
  }

  return (
    <div>
      <p className="my-activity" style={{ top: 30 }}>
        My Activities
      </p>
      {/* <span className="arrow arrow1">&#8594;</span> */}
      <div className="steps" style={{ top: 150 }}>
        <p className="title">Steps</p>
        <div className="circle-wrap">
          <div className="circle">
            <div className="mask full">
              <div className="fill"></div>
            </div>
            <div className="mask half">
              <div className="fill"></div>
            </div>
            <section className="inside-circle ">
              <section className="count" id="lungesCount">
                0<br />
                <span>Steps</span>
              </section>
            </section>
          </div>
        </div>
      </div>
      <div className="heart" style={{ top: 150 }}>
        <p className="title">Heart</p>
        <div>
          <div className="heartpluse"></div>
          <section className="heart_count" id="pushupsCount">
            0<span> Bpm</span>
          </section>
        </div>
      </div>
      <div className="spo2" style={{ top: 350 }}>
        <p className="title">SPO2</p>
      </div>
      <div className="calories" style={{ top: 300 }}>
        <p className="title">Calories</p>
        <div className="circle-wrap">
          <div className="circle">
            <div className="mask full">
              <div className="fill"></div>
            </div>
            <div className="mask half">
              <div className="fill"></div>
            </div>
            <section className="inside-circleforcalories ">
              <section className="count" id="crunchesCount">
                0<br />
                <span>Kcal</span>
              </section>
            </section>
          </div>
        </div>
      </div>
      <div id="navbar">
        {/* <button
          className="button"
          id="connectBtn"
          onClick={() => requestDevice()}
        >
          Connect
        </button> */}
        {/* <!-- Connect Button --> */}
        <button
          className="button daily"
          id="startBtn"
          onClick={() => startActivity()}
          style={{ width: 100 }}
        >
          Start
        </button>
        {/* <!-- Start Button --> */}
        <button
          className="button daily"
          id="stopBtn"
          onClick={() => stopActivity()}
          style={{ width: 100 }}
        >
          Stop
        </button>
        {/* <!-- Stop Button --> */}
        {/* <Link to="/" className="button" id="backBtn">
          Back
        </Link> */}
        {/* <!-- Back Button --> */}
      </div>
      <div className="footer" style={{ top: 650 }}>
        <nav>
          <section
            style={{ display: "flex", justifyContent: "space-around" }}
            className="footercontent"
          >
            <div className="home">
              <Link to="/mainpage">
                {" "}
                <img
                  src={Homeicon}
                  height="20"
                  width="20"
                  className="homeicon"
                />
              </Link>
            </div>

            <div className="startworkout">
              <Link to="/workputpage">
                {" "}
                <img src={startworkout} height="20" width="20" />
              </Link>
            </div>

            <div className="connect">
              <img
                src={connect}
                height="20"
                width="20"
                className="button"
                id="connectBtn"
                onClick={() => requestDevice()}
              />
            </div>
          </section>
        </nav>
      </div>
    </div>
  );
}

export default Workoutpage;
