import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import startworkout from "./exercise.svg";
import Homeicon from "./icon.svg";
import connect from "./watch.svg";
import logout from "./logout-10.svg";
import firebase from "./firebase";
import { useNavigate } from "react-router-dom";
import "firebase/compat/auth";

//create a reference to the data
var ref = firebase.database().ref("activity");

function Mainpage(props) {
  const pushupsCounter = null;
  const lungesCounter = null;
  const crunchesCounter = null;
  const [signedIn, setSignedIn] = useState(false);
  ref.on(
    "value",
    (snapshot) => {
      const data = snapshot.val();
      console.log(data);
      // Do something with the activity data
    },
    (error) => {
      console.error(error);
    }
  );

  //signout
  const handleSignOut = () => {
    const user = firebase.auth().currentUser;
    if (user) {
      firebase
        .auth()
        .signOut()
        .then(() => {
          firebase
            .database()
            .ref("users/" + user.uid)
            .update({
              signedIn: false, // Set the signedIn property to false
            });
          setSignedIn(false); // Update the state in your component
          console.log("logged out");
          navigate("/"); // Redirect the user to the login page after signing out
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      console.log("No user signed in.");
    }
  };

  let navigate = useNavigate();

  return (
    <div>
      <p className="my-activity">My Activities</p>
      <div className="avatar"></div>
      <p className="weekly_activity">Weekly Activity</p>
      {/* <div className="arrow">&#8594;</div> */}
      <div className="buttons">
        <button className="daily">MON</button>
        <button className="daily">TUE</button>
        <button className="daily">WED</button>
        <button className="daily">THUR</button>
      </div>
      <p className="todays-activity">Today's Activity</p>
      // components
      <Lunges lungesCounter />
      <PushUps pushupsCounter />
      <div className="spo2">
        <p className="title">SPO2</p>
      </div>
      <Crunches crunchesCounter />
      <div className="footer">
        <nav>
          <section
            style={{ display: "flex", justifyContent: "space-around" }}
            className="footercontent"
          >
            <div className="home">
              <Link to="/mainpage">
                {" "}
                <img src={Homeicon} height="20" width="20" />
              </Link>
            </div>

            <div className="startworkout">
              <Link to="/workputpage">
                {" "}
                <img src={startworkout} height="20" width="20" />
              </Link>
            </div>
            <div className="logout">
              <img
                src={logout}
                height="20"
                width="20"
                className="sign_out"
                onClick={handleSignOut}
              />
            </div>
          </section>
        </nav>
      </div>
    </div>
  );
}
function Lunges(props) {
  return (
    <div className="steps">
      <p className="title">Steps</p>

      <div className="circle-wrap">
        <div className="circle">
          <div className="mask full">
            <div className="fill"></div>
          </div>
          <div className="mask half">
            <div className="fill"></div>
          </div>
          <section className="inside-circle ">
            <section className="count">
              {props.lungesCounter}
              <br />
              <span>Steps</span>
            </section>
          </section>
        </div>
      </div>
    </div>
  );
}
function PushUps(props) {
  return (
    <div className="heart">
      <p className="title">Heart</p>
      <div>
        <div className="heartpluse"></div>
        <section className="heart_count">{props.pushupsCounter}</section>
      </div>
    </div>
  );
}

function Crunches(props) {
  return (
    <div className="calories">
      <p className="title">Calories</p>
      <div className="circle-wrap">
        <div className="circle">
          <div className="mask full">
            <div className="fill"></div>
          </div>
          <div className="mask half">
            <div className="fill"></div>
          </div>
          <section className="inside-circleforcalories  ">
            <section className="count">
              {props.crunchesCounter} <br />
              <span>Kcal</span>
            </section>
          </section>
        </div>
      </div>
    </div>
  );
}
export default Mainpage;
