import React from "react";
import { useNavigate } from "react-router-dom";
function Firstpage() {
  let navigate = useNavigate();
  return (
    <div className="container">
      <div className="top">
        <div className="purple_circle"></div>
        <div className="green_shade"></div>
        <div className="black_circle">
          <p>Welcome Back</p>
        </div>
      </div>

      <div className="bottom">
        <button
          className="sign_in"
          onClick={() => {
            navigate("/LogIn");
          }}
        >
          Log In
          <div className="mini_circle"> </div>
        </button>

        <button
          className="sign_up"
          onClick={() => {
            navigate("/SignUp");
          }}
        >
          <p className="signintext">Sign Up</p>
          {/* <Span className="arrow"> &#8594</Span> */}
        </button>
      </div>
    </div>
  );
}

export default Firstpage;
