import React from "react";
import { useNavigate } from "react-router-dom";

import { Link } from "react-router-dom";
import { auth, provider } from "./App.js";
import { signInWithPopup } from "firebase/auth";

import { Icon } from "react-icons-kit";
import { eyeOff } from "react-icons-kit/feather/eyeOff";
import { eye } from "react-icons-kit/feather/eye";
import { useEffect, useState } from "react";
//second pagee
function LogIn(props) {
  let navigate = useNavigate();

  //password hide or unhide
  const [type, setType] = useState("password");
  const [icon, setIcon] = useState(eyeOff);
  const handleToggle = () => {
    if (type === "password") {
      setIcon(eye);
      setType("text");
    } else {
      setIcon(eyeOff);
      setType("password");
    }
  };

  // usual sign in
  const handleloginSubmit = async (event) => {
    event.preventDefault();
    const email = event.target.querySelector(".user_email").value;
    const password = event.target.querySelector(".user_password").value;
    const loginSuccess = await props.login(event, email, password);

    if (loginSuccess) {
      navigate("/mainpage");
    } else {
      console.log("Login failed");
    }
  };

  // google sign in
  const SignInWithGoogle = () => {
    signInWithPopup(auth, provider)
      .then((result) => {
        const name = result.user.displayName;
        const email = result.user.email;
        console.log(result);
        localStorage.setItem("name", name);
        localStorage.setItem("email", email);

        navigate("/mainpage"); // navigate to MainPage component
      })
      .catch((error) => {
        console.log(error);
        alert("Error signing in with Google"); // display error message
      });
  };
  return (
    <>
      <div className="container">
        <div className="top">
          <div className="purple_circlep2"></div>
          <div className="green_shadep2"></div>
          <div className="black_circlep2"></div>
        </div>

        <div className="middle">
          <span className="sign_intext">Sign In</span>
          <form action="" onSubmit={handleloginSubmit}>
            <label className="email_address">Email Address</label>
            <input
              type="email"
              className="user_email"
              placeholder="abc@gmail.com"
            />
            <label className="password">Password</label>
            <input type={type} className="user_password" />
            <div className="icon">
              <span onClick={handleToggle}>
                <Icon icon={icon} size={20} />
              </span>
            </div>
            <a href="" className="forgot_password">
              Forgot Password
            </a>
            <div className="bottom">
              <button
                type="submit"
                className="sign_in sign_in2"
                // onClick={() => {
                //   navigate("/mainpage");
                // }}
              >
                Sign In
                {/* <div class="mini_circle"> <Span class="arrow1 arrow"> &#8594</Span></div> */}
              </button>
              {/* <p class="message">{props.message}</p> */}
            </div>
          </form>

          <p className="acct">
            Don't have an account? <Link to="/signup">Sign Up</Link>
            <br />
            OR
          </p>

          <button onClick={SignInWithGoogle} className="login-with-google-btn">
            Sign in with Google
          </button>
        </div>
      </div>
    </>
  );
}

export default LogIn;
