import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";
function SignUp(props) {
  let navigate = useNavigate();

  const handleSubmit = async (event) => {
    event.preventDefault();
    const email = event.target.querySelector(".user_email").value;
    const password = event.target.querySelector(".user_password").value;
    const signupSuccess = await props.register(event, email, password); // pass the event object along with email and password

    if (signupSuccess) {
      navigate("/mainpage");
    } else {
      console.log("Login failed");
    }
  };

  return (
    <div className="container">
      <div className="top">
        <div className="purple_circlep2"></div>
        <div className="green_shadep2"></div>
        <div className="black_circlep2"></div>
      </div>

      <div className="middle">
        <span className="sign_intext">SignUp</span>
        <form onSubmit={handleSubmit}>
          <label className="email_address">Email Address</label>
          <input
            type="email"
            className="user_email"
            placeholder="abc@gmail.com"
          />
          <label className="password">Password</label>
          <input type="password" className="user_password" />
          <a href="" className="forgot_password">
            Forgot Password
          </a>
          <div className="bottom">
            <button className="sign_in sign_in2" type="submit">
              Sign Up
            </button>
            <p className="message">{props.message}</p>
            <p className="acct">
              Already have an account? <Link to="/login">Sign in</Link>
            </p>
          </div>
        </form>
      </div>
    </div>
  );
}

export default SignUp;
